.contactWrapper {
    width : 100vw;
    height : 100vh;

    transition: transform 350ms, opacity 350ms;
    will-change: opacity, transform;

    transform: scale(0);
    z-index : 2500;
    opacity : 0;

    overflow: hidden;
    position: fixed;
    
    background-color: rgba(17, 17, 17, 0.932);

    display : flex;
    flex-direction: column;
    align-items: center;

    .closeButton {
        cursor: pointer;
        margin-top: 5vh;
        background-color: transparent;
        color : gray;
        font-weight: 400;
        border : none;
        font-size : 2.5vh;

        transition: 250ms color;
        will-change: color;
    }

    .closeButton:hover {
        color : white;
    }

    .contactTitle {
        color : white;
        font-weight: 400;
        font-size : 3.25vh;
    }

    .contactTitle::after {
        width : 60%;
        height : 0.25vh;
        content: '';
        display : block;
        background-color: gray;
        margin-left : 20%;
        margin-top : 5%;
    }

    .contactP {
        text-align: center;
        color : whitesmoke;
        max-width : 30vw;
        font-size : 2.2vh;
        font-weight: 300;
        margin-top : 2vh;
        
        @media screen and (orientation : portrait) {
            max-width : 75vw;
        }
    }

    .contactForm {
        margin-top : 2vh;
        width : 30vw;
        height : 40vh;
        display : flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

         @media screen and (orientation : portrait) {
            width : 75vw;
        }

        .contactInput, .contactTextArea {
            background-color: rgba(71, 71, 71, 0.747);
            border : none;
            color : whitesmoke;
            padding : 1vh;
            font-size : 1.75vh;
            outline : none;
        }
        .contactInput {
            width : 100%;
            height : 15%;
        }
        .contactTextArea {
            width : 100%;
            height : 30%;
        }

        .contactSubmit {
            width : 100%;
            height : 15%;
            color : white;
            font-size : 2vh;
            background-color: rgb(52, 171, 240);
            border : none;
            cursor: pointer;

            transition: background-color 250ms;
            will-change: background-color;
        }

        .contactSubmit:hover {
            background-color: rgb(42, 143, 202);
        }
    }
}

.shown {
    opacity : 1;
    transform: scale(1);
    z-index: 2500;
}