.m0 {
  margin-left: 0;
  margin-right: 0;
}

.cafe {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../../images/coffee.jpg");
  margin-right: 2vh;
}

.ideas {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../../images/ideas.jpg");
  margin-right: 2vh;
}

.photo {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../../images/photo.jpg");
  margin-right: 2vh;
}

.sketch1 {
  background-image: url("../../images/sketch.jpg");
  background-size: 150%;
  background-position: center;
}

.ideas1 {
  background-image: url("../../images/ideas.jpg");
  background-size: 150%;
  background-position: center;
}

.photo1 {
  background-image: url("../../images/photo.jpg");
  background-size: 150%;
  background-position: center;
}

.sketch {
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../../images/sketch.jpg");
  margin-left: 2vh;
}

.portfolioElement {
  width: 30vh;
  height: 25vh;
  background-size: cover;
  cursor: pointer;
  transition: filter 250ms;
  will-change: filter;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
@media screen and (orientation: portrait) {
  .portfolioElement {
    width: 80vw;
    height: 28vh;
  }
  .portfolioElement h1 {
    align-self: flex-start;
  }
}
.portfolioElement .cat1 {
  background-color: rgb(216, 73, 209);
}
.portfolioElement .cat2 {
  background-color: rgb(53, 199, 48);
}
.portfolioElement .category {
  width: 32%;
  height: 12%;
  color: white;
  border-radius: 5vh;
  margin: 1vh;
  font-size: 1.35vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.portfolioElement .catTitle {
  margin: 0;
  color: white;
  font-size: 2.25vh;
  max-width: 30vh;
  width: 30vh;
  padding: 1.5vh;
}

.portfolioElement:hover {
  filter: brightness(105%);
}

@media screen and (orientation: portrait) {
  .cafe,
  .photo {
    margin-right: 0vh;
  }
  .sketch,
  .ideas {
    margin-left: 0vh;
  }
}
.portfolioContent {
  width: 100vw;
  height: 32vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .portfolioContent {
    height: 65vh;
    flex-direction: column;
    justify-content: space-around;
  }
  .portfolioContent .cafe,
  .portfolioContent .photo {
    margin-right: 0vh;
  }
  .portfolioContent .sketch,
  .portfolioContent .ideas {
    margin-left: 0vh;
  }
}

#pc1 {
  margin-right: 2vh;
}

#pc2 {
  margin-left: 2vh;
}

@media screen and (orientation: portrait) {
  #pc1 {
    margin-right: 0vh;
  }
  #pc2 {
    margin-left: 0vh;
  }
}/*# sourceMappingURL=portfolio.css.map */