.blog {
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../images/deskblog.jpg");
  background-position: center;
}

.blogElement {
  width: 30vh;
  height: 32vh;
  cursor : pointer;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-top : 2vh;
  padding-bottom: 2vh;

  .blogtitle {
    margin : 0;
    color : black;
    font-weight: 500;
    font-size: 2vh;
    width : 100%;
    max-width : 100%;
  }

  .subtitle {
    margin : 0;
    color : rgb(105, 105, 105);
    font-weight: 400;
    font-size: 1.6vh;
    width : 100%;
    max-width : 100%;
  }

  .writtenBy {
    margin : 0;
    color : gray;
    font-weight: 300;
    font-style: italic;
    font-size: 1.35vh;
    width : 100%;
    max-width : 100%;
  }

  .photo {
    background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
      url("../../images/photo.jpg");
  }

  .ideas {
    background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
      url("../../images/ideas.jpg");
  }

  .blogImage {
    width: 100%;
    height: 50%;

    transition: filter 250ms;
    will-change: filter;

    display : flex;
    justify-content: space-between;
    background-size: cover ;

    .date {
        color : white;
        font-size : 1.5vh;
        font-weight: 400;
        margin: 1vh;
    }

    .category {
      width: 30%;
      height: 15%;
      color: white;
      border-radius: 5vh;
      margin: 1vh;
      padding : 0.1vh;
      font-size: 1.35vh;

      display: flex;
      justify-content: center;
      align-items: center;
    }

    .cat3 {
        background-color: rgb(34, 110, 224);
    }

    .cat4 {
        background-color: rgb(206, 60, 194);
    }

  }
}

.blogElement:hover {
    .blogImage {
        filter: brightness(110%);
    }
}
