footer {
    width : 100%;
    height : 10vh;
    overflow : hidden;
    background-color: #333;
    z-index : 2400;

    display : flex;
    justify-content: space-around;
    align-items: center;

    @media screen and (orientation : portrait) {
        height : 35vh;
    }

     .navigationSection {
      width: 100vw;
      height: 10vh;
      display: flex;
      justify-content: space-around;
      align-items: center;

      padding-left: 10vw;
      padding-right: 10vw;

       @media screen and (orientation : portrait) {
        height : 35vh;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        }

      .navButtons {
        width: 28vw;
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;

        @media screen and (orientation : portrait) {
            height : 60%;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
        }

        .navButton {
          background-color: transparent;
          color: white;
          border: none;
          cursor: pointer;
          font-size: 1.6vh;
          font-weight: 400;
          color: rgb(209, 209, 209);

          transition: color 250ms;
          will-change: color;
        }

        .navButton:hover {
          color: white;
        }

        .choosenButton {
          font-weight: 500;
          color: white;
        }

        .diffButton {
          background-color: rgba(138, 138, 138, 0.65);
          padding: 1vh;
          transition: background-color 250ms, color 250ms;
          will-change: background-color, color;
        }

        .diffButton:hover {
          background-color: rgba(138, 138, 138, 0.89);
        }
      }

      .logo {
        font-size: 3.25vh;
        color: white;
        font-weight: 400;
        margin: 0;

        @media screen and (orientation : portrait) {
            height : 1%;
        }

        .bolder {
          font-weight: 600;
        }
      }
    }

}

.footerH3 {
  color : white;
  font-size : 2vh;
}