.articleWrapper {
  width: 100vw;
  height: auto;
}
.articleWrapper .article {
  width: 100vw;
  height: auto;
  margin-bottom: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.articleWrapper .article .articleImage {
  width: 82vh;
  height: 42vh;
  z-index: 1;
  border: solid whitesmoke 1vh;
  transform: translateY(-5vh);
}
@media screen and (orientation: portrait) {
  .articleWrapper .article .articleImage {
    width: 50vh;
    height: 25vh;
  }
}
.articleWrapper .article .articleTitle {
  font-size: 2.8vh;
  width: 82vh;
}
@media screen and (orientation: portrait) {
  .articleWrapper .article .articleTitle {
    width: 90vw;
  }
}
.articleWrapper .article .articleParagraph {
  max-width: 82vh;
  margin-top: 2vh;
  margin-bottom: 2vh;
  font-size: 1.8vh;
}
@media screen and (orientation: portrait) {
  .articleWrapper .article .articleParagraph {
    max-width: 90vw;
  }
}
.articleWrapper .recentProjects {
  width: 100vw;
  height: 40vh;
  background-color: rgb(221, 221, 221);
}
@media screen and (orientation: portrait) {
  .articleWrapper .recentProjects {
    height: 45vh;
  }
}
.articleWrapper .recentProjects .flex {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 3.5vh;
}
.articleWrapper .recentProjects .projectsTitle {
  text-align: center;
  font-size: 3vh;
  font-weight: 500;
  padding-top: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (orientation: portrait) {
  .articleWrapper .recentProjects .projectsTitle {
    font-size: 3.4vh;
  }
}
.articleWrapper .recentProjects .projectsTitle::after {
  width: 5vw;
  height: 0.25vh;
  background-color: black;
  content: "";
  display: block;
  margin-top: 0.35vh;
}
@media screen and (orientation: portrait) {
  .articleWrapper .recentProjects .projectsTitle::after {
    width: 20vw;
  }
}

.border {
  width: 100vh;
  height: 10vh;
  background-color: whitesmoke;
}
@media screen and (orientation: portrait) {
  .border {
    width: 100vw;
    margin-left: 0;
  }
}

.translate6 {
  transform: translateY(-8vh);
}

.translate10 {
  transform: translateY(-10vh);
}/*# sourceMappingURL=article.css.map */