footer {
  width: 100%;
  height: 10vh;
  overflow: hidden;
  background-color: #333;
  z-index: 2400;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media screen and (orientation: portrait) {
  footer {
    height: 35vh;
  }
}
footer .navigationSection {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10vw;
  padding-right: 10vw;
}
@media screen and (orientation: portrait) {
  footer .navigationSection {
    height: 35vh;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}
footer .navigationSection .navButtons {
  width: 28vw;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media screen and (orientation: portrait) {
  footer .navigationSection .navButtons {
    height: 60%;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
}
footer .navigationSection .navButtons .navButton {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.6vh;
  font-weight: 400;
  color: rgb(209, 209, 209);
  transition: color 250ms;
  will-change: color;
}
footer .navigationSection .navButtons .navButton:hover {
  color: white;
}
footer .navigationSection .navButtons .choosenButton {
  font-weight: 500;
  color: white;
}
footer .navigationSection .navButtons .diffButton {
  background-color: rgba(138, 138, 138, 0.65);
  padding: 1vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
}
footer .navigationSection .navButtons .diffButton:hover {
  background-color: rgba(138, 138, 138, 0.89);
}
footer .navigationSection .logo {
  font-size: 3.25vh;
  color: white;
  font-weight: 400;
  margin: 0;
}
@media screen and (orientation: portrait) {
  footer .navigationSection .logo {
    height: 1%;
  }
}
footer .navigationSection .logo .bolder {
  font-weight: 600;
}

.footerH3 {
  color: white;
  font-size: 2vh;
}/*# sourceMappingURL=footer.css.map */