.workTogether {
  width: 100vw;
  height: 21vh;
  background-color: rgb(48, 223, 121);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 3vh;
}
.workTogether h3 {
  text-align: center;
  color: white;
  margin: 0;
  font-size: 3.5vh;
}
.workTogether button {
  width: 9vw;
  height: 5.5vh;
  border: none;
  background-color: rgba(0, 0, 0, 0.356);
  color: white;
  font-size: 1.5vh;
  cursor: pointer;
}
@media screen and (orientation: portrait) {
  .workTogether button {
    width: 13vh;
  }
}/*# sourceMappingURL=together.css.map */