.hamburgerMenu {
  width: 8vh;
  height: 8vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
.hamburgerMenu .stripe {
  width: 100%;
  height: 12%;
  background-color: white;
  display: block;
  transition: transform 250ms, background-color 250ms;
}
.hamburgerMenu #str1 {
  transform-origin: left;
}
.hamburgerMenu #str3 {
  transform-origin: left;
}
.hamburgerMenu .rot1 {
  transform: rotate(45deg);
}
.hamburgerMenu .rot2 {
  background-color: transparent;
}
.hamburgerMenu .rot3 {
  transform: rotate(-45deg) translateX(-3px);
}

.hamburgerButtons {
  position: fixed;
  width: 100vw;
  height: 50vh;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  transform: translateY(-50vh);
  background-color: #333;
  transition: transform 250ms;
  will-change: transform;
}
.hamburgerButtons .navButton {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.6vh;
  font-weight: 400;
  color: rgb(209, 209, 209);
  z-index: 1001;
  transition: color 250ms;
  will-change: color;
}
.hamburgerButtons .navButton:hover {
  color: white;
}
.hamburgerButtons .choosenButton {
  font-weight: 500;
  color: white;
}
.hamburgerButtons .diffButton {
  background-color: rgba(138, 138, 138, 0.65);
  padding: 1vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
}
.hamburgerButtons .diffButton:hover {
  background-color: rgba(138, 138, 138, 0.89);
}

.hamburgerShown {
  transform: translateY(12vh);
}

.navFixed {
  position: fixed;
  z-index: 1000;
  background-color: #333;
}

.img1, .img2, .img3 {
  transition: filter 250ms;
  opacity: 0;
}

.img1:hover, .img2:hover, .img3:hover {
  filter: brightness(110%);
}

#i1 {
  animation: appear 0.4s;
}

#i2 {
  animation: appear 0.4s;
  animation-delay: 0.1s;
}

#i3 {
  animation: appear 0.4s;
  animation-delay: 0.2s;
}

#i4 {
  animation: appear 0.4s;
  animation-delay: 0.3s;
}

@keyframes appear {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes appear2 {
  from {
