.homeWrapper, .portfolioWrapper, .articleWrapper {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
  overflow-y: hidden;

  @media screen and (orientation: portrait) {
    height: auto;
    width: 100vw;
  }

  .homeTiles {
    width: 50vw;
    height: 64vh;
    margin-left: 25vw;
    background-color: whitesmoke;
    transform: translateY(-5vh);

    @media screen and (orientation: portrait) {
      height: 100vh;
      width: 100vw;
      margin-left: 0;
       transform: translateY(0vh);
    }

    @mixin imgContent() {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }

    @mixin imgParagraph() {
      color: white;
      font-size: 3vh;
      font-weight: 600;
      word-break: break-all;
      width: 100%;
      max-width: 100%;
      padding-left: 1.5vh;
      padding-bottom: 1.5vh;
    }

    .topTiles {
      width: 50vw;
      height: 32vh;
      display: flex;
      justify-content: space-around;
      align-items: center;

      @media screen and (orientation: portrait) {
        height: 50%;
        width: 100vw;
        flex-direction: column;
      }

      .img1 {
        width: 55%;
        height: 100%;
        border: solid whitesmoke 1vh;
        border-right: 0.5vh solid whitesmoke;
        cursor: pointer;
        @include imgContent();
        p {
          @include imgParagraph();
        }

        @media screen and (orientation: portrait) {
          height: 100%;
          width: 100%;
          flex-direction: column;
          border-right: 1vh solid whitesmoke;
        }
      }
      .img2 {
        width: 45%;
        height: 100%;
        border: solid whitesmoke 1vh;
        border-left: 0.5vh solid whitesmoke;
        cursor: pointer;
       
        @include imgContent();
        p {
          @include imgParagraph();
        }

        @media screen and (orientation: portrait) {
          height: 100%;
          width: 100%;
          flex-direction: column;
          border-left: 1vh solid whitesmoke;
          border-top: 0;
        }
      }
    }
    .bottomTiles {
      width: 50vw;
      height: 32vh;
      display: flex;
      align-items: center;

      @media screen and (orientation: portrait) {
        width: 100vw;
        height: 50%;
        flex-direction: column;
      }

      .img2 {
        width: 40%;
        height: 100%;
        border: solid whitesmoke 1vh;
        border-right: 0.5vh solid whitesmoke;
        border-top: none;
        cursor: pointer;
        @include imgContent();
        p {
          @include imgParagraph();
        }

        @media screen and (orientation: portrait) {
          height: 100%;
          width: 100%;
          flex-direction: column;
          border-right: 1vh solid whitesmoke;
        }
      }

      .img3 {
        width: 40%;
        height: 100%;
        border: solid whitesmoke 1vh;
        border-left: 0.5vh solid whitesmoke;
        border-top: none;
        cursor: pointer;
        @include imgContent();
        p {
          @include imgParagraph();
        }

        @media screen and (orientation: portrait) {
          height: 100%;
          width: 100%;
          flex-direction: column;
          border-left: 1vh solid whitesmoke;
        }
      }
    }
  }

  .about {
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../../images/about1.jpg');
    background-position: center;
  }

  .coffee {
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../../images/coffee.jpg');
    background-position: center;
  }

  .portfolio {
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../../images/desk.jpg');
    background-position: center;
    background-size: 120%;

    @media screen and (orientation : portrait) {
        background-size: 165%;
    }
  }

  .title {
    text-align: center;
    margin: 0;
    font-weight: 400;
    color: white;
    font-size: 5.25vh;
    padding-top: 6vh;

    animation : appear2 .6s;

    @media screen and (orientation: portrait) {
      font-size: 3.4vh;
    }
  }
}

 .navbarWrapper {
    width: 100vw;
    height: 38vh;
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
      url("../../images/desk.webp");

    background-size: 100%;

    @media screen and (orientation: portrait) {
      background-size: 135%;
      background-position: right;
    }

    .navigationSection {
      width: 100vw;
      height: 12vh;
      display: flex;
      justify-content: space-around;
      align-items: center;

      transition: background-color 250ms;
      will-change: background-color;

      @media screen and (orientation: portrait) {
        justify-content: space-between;
      }

      padding-left: 10vw;
      padding-right: 10vw;
    }

    .navButtons {
      width: 28vw;
      height: 100%;
      display: flex;
      justify-content: space-around;
      align-items: center;

      .navButton {
        background-color: transparent;
        color: white;
        border: none;
        cursor: pointer;
        font-size: 1.6vh;
        font-weight: 400;
        color: rgb(209, 209, 209);

        transition: color 250ms;
        will-change: color;
      }

      .navButton:hover {
        color: white;
      }

      .choosenButton {
        font-weight: 500;
        color: white;
      }

      .diffButton {
        background-color: rgba(138, 138, 138, 0.65);
        padding: 1vh;
        transition: background-color 250ms, color 250ms;
        will-change: background-color, color;
      }

      .diffButton:hover {
        background-color: rgba(138, 138, 138, 0.89);
      }
    }

    .logo {
      font-size: 3.25vh;
      color: white;
      font-weight: 400;
      margin: 0;
      cursor: pointer;

      .bolder {
        font-weight: 600;
      }
    }
  }

.TextContent {
  height: auto;
  padding : 5vh;
  padding-left : 25vw;
  padding-right : 25vw;

  @media screen and (orientation : portrait) {
    padding-left : 10vw;
    padding-right: 10vw;
  }

  h2 {
    font-size : 4vh;
    font-weight: 500;
  }

  blockquote {
    font-size : 1.9vh;
    color: rgb(163, 163, 163);
    border-left : solid rgb(163, 163, 163) 0.6vh;
    padding-left : 1vh;
  }

  p {
    font-size : 1.9vh;
    margin-bottom : 1vh;
    margin-top : 1vh;
    a {
      color : green;
    }
    img {
      width : 33vh;
      height : 40vh;
      float: right;
      margin-left : 1vw;

       @media screen and (orientation : portrait) {
        width : 25vh;
        height : 30vh;
      }
    }
  }
}

@mixin image($url, $size) {
  background-image: url($url);
  background-size: $size;
  background-position: center;
}

#i1 {
  @include image("../../images/about.jpg", 210%);
}

#i2 {
  @include image("../../images/coffee.jpg", 210%);
}

#i3 {
  @include image("../../images/photo.jpg", 280%);
}

#i4 {
  @include image("../../images/camera.jpg", 250%);
}

.hamburgerMenu {
  width: 8vh;
  height: 8vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  cursor: pointer;

  .stripe {
    width: 100%;
    height: 12%;
    background-color: white;
    display: block;

    transition: transform 250ms, background-color 250ms;
  }

  #str1 {
    transform-origin: left;
  }

  #str3 {
    transform-origin: left;
  }

  .rot1 {
    transform: rotate(45deg);
  }

  .rot2 {
    background-color: transparent;
  }

  .rot3 {
    transform: rotate(-45deg) translateX(-3px);
  }
}

.hamburgerButtons {
  position: fixed;
  width: 100vw;
  height: 50vh;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  transform: translateY(-50vh);

  background-color: #333;
  transition: transform 250ms;
  will-change: transform;

  .navButton {
    background-color: transparent;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 1.6vh;
    font-weight: 400;
    color: rgb(209, 209, 209);
    z-index : 1001;

    transition: color 250ms;
    will-change: color;
  }

  .navButton:hover {
    color: white;
  }

  .choosenButton {
    font-weight: 500;
    color: white;
  }

  .diffButton {
    background-color: rgba(138, 138, 138, 0.65);
    padding: 1vh;
    transition: background-color 250ms, color 250ms;
    will-change: background-color, color;
  }

  .diffButton:hover {
    background-color: rgba(138, 138, 138, 0.89);
  }
}

.hamburgerShown {
  transform: translateY(12vh);
}

.navFixed {
  position: fixed;
  z-index: 1000;
  background-color: #333;
}

.img1,.img2,.img3 {
  transition: filter 250ms;
  opacity : 0;
}

.img1:hover,.img2:hover,.img3:hover {
  filter: brightness(110%);
}

#i1 {
   animation: appear 0.4s;
}

#i2 {
  animation: appear 0.4s;
  animation-delay : .1s;
}

#i3 {
  animation: appear 0.4s;
  animation-delay : .2s;
}

#i4 {
  animation: appear 0.4s;
  animation-delay: .3s;
}

@keyframes appear {
  from {
    opacity : 0;
    transform: scale(0.9);
  }

  to {
    opacity : 1;
    transform: scale(1);
  }
}

@keyframes appear2 {
  from {
    opacity : 0;
    transform: translateY(-5vh);
  }

  to {
    opacity : 1;
    transform: translateY(0vh);
  }
}

.op {
  opacity : 1;
}
